.footer__top {
  width: 100%;
  padding: 100px 50px;
  box-sizing: border-box;
  border-top: 1px solid #f0ece8;
  border-bottom: 1px solid #f0ece8;

  display: flex;
  justify-content: center;
  gap: 30px;
}

@media screen and (max-width: 1440px) {
  .footer__top {
    gap: 100px;
    flex-wrap: wrap;
  }
}

@media screen and (max-width: 1024px) {
  .footer__top {
    gap: 75px;
    padding: 75px;
  }
}

@media screen and (max-width: 768px) {
  .footer__top {
    display: grid;
    gap: 50px;
    padding: 50px 0;
  }
}

.footer__list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  width: 100%;
  max-width: 300px;
  display: grid;
  align-self: flex-start;
  /*align-items: center; */
  justify-items: center;
  gap: 30px;

  text-transform: uppercase;
  font-style: normal;
  font-weight: 600;
  font-size: 20px;
  line-height: 22px;
  color: #000;
}

.footer__title {
  margin: 0;
  padding: 0;
  text-transform: uppercase;
  font-style: normal;
  font-weight: 600;
  font-size: 20px;
  line-height: 22px;
  color: #000;
  text-align: center;
}

.footer__text {
  margin: 0;
  text-transform: none;
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 22px;
  color: #6e6e6e;
}

.footer__list-link {
  color: #6e6e6e;
  transition: 300ms ease;
}

.footer__list_link:hover {
  color: #ff9c08;
}

