.footer__link {
  text-decoration: none;
  text-transform: none;
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 22px;
  color: #6e6e6e;

  transition: 300ms ease;
}

.footer__link:hover {
  cursor: pointer;
  color: #ff9c08;
  transform: translate3d(0px, -3px, 0.01px);
}

.footer__icon-links {
  margin: 0;
  padding: 0;
  list-style-type: none;

  display: grid;
  gap: 25px;
}

@media screen and (max-width: 768px) {
  .footer__icon-links {
    gap: 8px;
  }
}